.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 410px;
}

/* The animation code */
@keyframes spin {
  0% {
    transform: rotate(0deg);
    top: 200px;
  }
  25% {
    transform: rotate(90deg);
    top: 100px;
  }
  50% {
    transform: rotate(180deg);
    top: 0px;
  }
  75% {
    transform: rotate(270deg);
    top: 100px;
  }
  100% {
    transform: rotate(360deg);
    top: 200px;
  }
}
.line, .botline, .midline, .topline {
  width: 3px;
  height: 30px;
  background: #000000;
  position: absolute;
  left: 19px;
  transform: rotate(50deg);
  border-radius: 50%;
}

.oval {
  width: 42px;
  height: 210px;
  background: #ff9d4d;
  border-radius: 50%;
  z-index: 10;
  position: absolute;
  animation-name: spin;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.topline {
  top: 134.2105263158px;
}

.midline {
  top: 90px;
}

.botline {
  top: 45.7894736842px;
}

