$baguetteHeight: 210px;
$baguetteWidth: calc($baguetteHeight / 5);
$lineWidth: 3px;
$lineHeight: 30px;
$topAndBottomLineOffset: calc($baguetteHeight / 4.75);

$midLine: calc($baguetteHeight / 2 - $lineHeight / 2);
$topLine: $midLine + $topAndBottomLineOffset;
$botLine: $midLine - $topAndBottomLineOffset;

$startHeight: 0px;
$endHeight: 200px;
$midHeight: calc(($endHeight - $startHeight) / 2);

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: $endHeight + $baguetteHeight;
}

/* The animation code */
@keyframes spin {
  0%   {transform: rotate(0deg); top: $endHeight;}
  25%  {transform: rotate(90deg); top: $midHeight;}
  50%  {transform: rotate(180deg); top: $startHeight;}
  75%  {transform: rotate(270deg); top: $midHeight}
  100% {transform: rotate(360deg); top: $endHeight;}
}

.line {
  width: $lineWidth;
  height: $lineHeight;
  background: #000000;
  position: absolute;
  left: calc($baguetteWidth / 2 - 2px);
  transform: rotate(50deg);
  border-radius: 50%;
}

.oval {
  width: $baguetteWidth;
  height: $baguetteHeight;
  background: #ff9d4d;
  border-radius: 50%;
  z-index: 10;
  position: absolute;
  animation-name: spin;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.topline {
  top: $topLine;
  @extend .line;
}

.midline {
  top: $midLine;
  @extend .line;
}

.botline {
  top: $botLine;
  @extend .line;
}
